<template>
    <el-dialog title="修改余额" :visible.sync="dialogBalance" class="updatePwd">
        <el-form :model="form">
            <el-form-item label="余额（元）:" :label-width="formLabelWidth">
                <el-input v-model="form.balance" auto-complete="off" :disabled="disabled"></el-input>
            </el-form-item>
            <el-form-item label="数额（元）:" :label-width="formLabelWidth">
                <el-input v-model="form.number_balance" auto-complete="off"></el-input>
            </el-form-item>
            <div class="radio-list">
                <el-radio class="radio" v-model="form.is_add" label="1">加</el-radio>
                <el-radio class="radio" v-model="form.is_add" label="2">减</el-radio>
            </div>
        </el-form>
        <div slot="footer" class="dialog-footer">
            <el-button @click="dialogBalance = false">取 消</el-button>
            <el-button type="primary" @click="submit()">提交</el-button>
        </div>
    </el-dialog>
</template>

<style scope="" lang="less" type="text/scss">
    .updatePwd{
        .radio-list{
            margin-left:120px;
        }
        .el-dialog__header{
            padding:30px;
            .el-dialog__headerbtn{
                width:50px;
                height:50px;
                margin:-30px -30px 0 0;
            }
        }
        .el-dialog__body{
            padding:30px 50px 0 0;
        }
    }
</style>

<script>
    import QuRequest from '../../../core/QuRequest'

    const quRequest = new QuRequest()

    export default {
        data() {
            return {
                dialogBalance: false,
                formLabelWidth: '120px',
                disabled:true,
                form:{
                    member_id: '',
                    balance: '',
                    number_balance: '',
                    is_add: '1',
                }
            }
        },
        methods: {
            showDialog(item) {
                if(item.member_id){
                    this.form.member_id = item.member_id;
                } else {
                    this.form.member_id = '';
                }
                this.form.balance = parseFloat(item.balance / 1000).toFixed(2);
                this.dialogBalance = true;
            },
            submit(){
                quRequest.send(quRequest.Apis.qutuiba.member.changeBalance,this.form
                ).then(res => {
                    this.$message.success('修改成功！');
                });
            }
        }
    }
</script>


